<template>
  <div>

    <v-btn v-if="!entryHtml" loading style="width:100%" />
    <div v-if="!!errorMessage">
      <v-icon>mdi-account-alert</v-icon>
      {{errorMessage}}
    </div>
    <div v-html="entryHtml" style="max-height: 99vh; overflow: auto;" />

  </div>

</template>

<script>
  import axios from 'axios';


  export default {
    props: ['category', 'title'],

    data: () => ({
      canLoad: true,
      errorMessage: '',
      entryHtml: ''
    }),

    mounted() {
      if (this.canLoad) {
        this.canLoad = false;

        axios({ method: "GET", url: "/api/l/toc/" + encodeURIComponent(this.category) + "/" + encodeURIComponent(this.title) })
                  .then(result => { this.entryHtml = result.data; },
                        error  => { this.canLoad = true;
                                    this.entryHtml = ' ';
                                    console.error(error);
                                    switch(error.response && error.response.status) {
                                      case 401: this.errorMessage = 'You do not have access to read this entry.'; break;
                                      case 404: this.errorMessage = 'Entry not found. The table of contents is probably broken.'; break;
                                      default : this.errorMessage = 'Could not load entry: ' + error.toString(); break;
                                    }
                                  });
      }
    }
  }

</script>
