<template>
  <v-container fluid>

  <v-progress-circular indeterminate style="margin: 15px 0 0 130px" v-if="this.loading"/>
  <span v-if="!this.loading && this.toc[this.category] && !this.toc[this.category].length">
    Categoria curentă nu conține nimic
  </span>
  <span v-if="this.errorMessage.length && !this.toc[this.category]" dark>
    <v-icon dark>mdi-alert</v-icon>
    {{ errorMessage }}
  </span>

  <span style="float:right">
    <span @click="lightPanels = false">Dark&nbsp;</span>
    <v-switch v-model="lightPanels" style="display: inline-block; vertical-align: middle" />
    <span @click="lightPanels = true">Light</span>
  </span>

  <v-expansion-panels popout :dark="!lightPanels">

    <v-expansion-panel
               hover focusable popout
               v-for="collection in toc[category]"
               :key="collection">

      <v-expansion-panel-header>
                {{ collection.title }}
      </v-expansion-panel-header>

      <v-expansion-panel-content class="outer-panel">
       <v-expansion-panels popout>

          <v-expansion-panel
                     hover focusable popout
                     v-for="title in collection.entryTitles"
                     :key="title">
                     <!-- (Above) Title is better than index, but there's still a slim possibility of collision with an entry with the same title in another category -->

            <v-expansion-panel-header>
                      {{ title }}
            </v-expansion-panel-header>

            <v-expansion-panel-content>
              <LiteratureEntry :category="category" :title="title" />
            </v-expansion-panel-content>
          </v-expansion-panel>

        </v-expansion-panels>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
  </v-container>

</template>


<script>
  import axios from 'axios';
  import router from '../plugins/router.js';
  import LiteratureEntry from '../components/LiteratureEntry.vue';

  export default {
    components: {LiteratureEntry},

    props: ['category'],

    data: () =>({
      lightPanels: false,
      loading: true,
      toc: {},
      errorMessage: ''
    }),

    methods: {
      isCurrentToc() {
        return !!this.toc[this.category];
      },
      
      loadTocIfNeeded(category) {
        if (!category) {
          router.replace('/l/Poezii');
    
        } else if (!this.isCurrentToc()) {
          this.loading = true;
          this.errorMessage = '';

          axios({ method: "GET", url: "/api/l/toc/" + encodeURIComponent(category)})
            .then(result => { this.$set(this.toc, category, result.data.d); this.loading=false; },
                  error  => { console.error(error);
                              this.loading=false;
                              this.errorMessage = 'Cuprinsul nu s-a putut încărca: ' + error.toString(); });
        }
      }
    },

    watch: {
      category(newVal) {
        this.loadTocIfNeeded(newVal);
      }
    },

    beforeMount () {
      this.loadTocIfNeeded(this.category);
    }
  }
</script>

<style>
  .outer-panel > .v-expansion-panel-content__wrap {
	padding-left: 0;
	padding-right: 0;
  }
</style>
